"use client";

import * as React from "react";
import { useQuery } from "@tanstack/react-query";
import { api } from "@/lib/api";
import LiveTradesTable from "@/components/ui/LiveTradesTable";
import type { KpiResponse } from "@/lib/types"; // or from api if you defined it there

function Kpi({ label, value, sub }: { label: string; value: React.ReactNode; sub?: string }) {
  return (
    <div className="rounded-xl border p-3">
      <div className="text-xs text-muted-foreground">{label}</div>
      <div className="text-lg font-semibold">{value ?? "—"}</div>
      {sub && <div className="text-[11px] text-muted-foreground mt-0.5">{sub}</div>}
    </div>
  );
}

export default function TradesPage() {
  // KPIs (hydrate once, then table can refresh/stream)
  const { data: kpis } = useQuery<KpiResponse>({
    queryKey: ["kpis"],
    queryFn: () => api.getKpis(),
    staleTime: 15_000,
    gcTime: 60_000,
  });

  // Trades list (polling; first page, large page size)
  // Trades list (polling; first page, large page size)
const PAGE = 1;
const PAGE_SIZE = 300;

const {
  data: rows = [],
  isLoading,
  error,
  refetch,
} = useQuery<any[], Error>({
  queryKey: ["trades", "list", PAGE, PAGE_SIZE],
  queryFn: () => api.getTrades(PAGE, PAGE_SIZE),
  select: (res: any) => (res?.items ?? []) as any[],
  placeholderData: [] as any[],    // must match the *selected* type
  refetchInterval: 5000,
  staleTime: 4000,                  // optional: helps dedupe
  gcTime: 60_000,
});


  return (
    <div className="p-4 max-w-[1400px] mx-auto">
      <h1 className="text-xl font-semibold mb-3">Live Intelligence</h1>

      {/* KPI row (Module 2) */}
      <div className="grid grid-cols-2 md:grid-cols-5 gap-3 mb-4">
        <Kpi label="Active creators" value={kpis?.active_creators} />
        <Kpi label="Buy %" value={kpis?.buy_pct != null ? `${kpis.buy_pct.toFixed(1)}%` : "—"} />
        <Kpi
          label="Avg blocks"
          value={kpis?.avg_blocks != null ? kpis.avg_blocks.toFixed(1) : "—"}
          sub="Negative = ahead (faster)"
        />
        <Kpi label="Avg exec score" value={kpis?.avg_score != null ? kpis.avg_score.toFixed(1) : "—"} />
        <Kpi label="TPS" value={kpis?.tps != null ? kpis.tps.toFixed(2) : "—"} />
      </div>

      {isLoading && <div>Loading…</div>}
      {error && (
        <div className="text-destructive mb-3">
          Failed to load.{" "}
          <button className="underline" onClick={() => refetch()}>
            Retry
          </button>
        </div>
      )}

      <LiveTradesTable rows={rows} pageSize={25} initialSortId="ts" initialSortDesc />
    </div>
  );
}
