"use client";

import * as React from "react";
import Link from "next/link";
import type { ColumnDef } from "@tanstack/react-table";

import { DataGrid } from "@/components/tables/data-grid";
import {
  colHash,
  colMono,
  colNumber,
  colDateTime,
  fmtNum,
  hashShort,
} from "@/components/tables/column-helpers";
import { ExecutionScoreBadge } from "@/components/ui/ExecutionScoreBadge";

/**
 * Normalized shape we render in the grid.
 * We map your raw /v1/trades rows into this before passing to DataGrid.
 */
export type TradeRow = {
  id: string;                 // trade_id (hash)
  ts: string | number | Date; // timestamp
  action: "BUY" | "SELL" | string;
  mint?: string | null;       // token/mint/symbol
  amount?: number | null;
  price?: number | null;
  exec_score?: number | null;
  copy_slot?: number | null;
  source_slot?: number | null;
  creator_pubkey?: string | null;
  wallet_id?: string | null;
};

/**
 * Build the column set once (stable reference).
 */
function useColumns(): ColumnDef<TradeRow, any>[] {
  return React.useMemo<ColumnDef<TradeRow, any>[]>(() => {
    return [
      // Trade hash
      colHash<TradeRow>("id", "Trade"),

      // Time
      colDateTime<TradeRow>("ts", "Time"),

      // Action chip
      {
        id: "action",
        accessorKey: "action",
        header: "Action",
        cell: (ctx) => {
          const v = String(ctx.getValue() ?? "").toUpperCase();
          const tone =
            v === "BUY"
              ? "bg-emerald-900/40 text-emerald-300"
              : v === "SELL"
              ? "bg-rose-900/40 text-rose-300"
              : "bg-muted";
          return (
            <span className={`px-2 py-0.5 rounded text-xs ${tone}`}>
              {v || "—"}
            </span>
          );
        },
        enableSorting: true,
      },

      // Token / mint
      colMono<TradeRow>("mint", "Token"),

      // Amount / Price
      colNumber<TradeRow>("amount", "Amount", 4),
      colNumber<TradeRow>("price", "Price", 6),

      // Execution score (badge)
      {
        id: "exec_score",
        accessorKey: "exec_score",
        header: "Exec",
        cell: (ctx) => (
          <ExecutionScoreBadge
            score={
              typeof ctx.getValue() === "number"
                ? (ctx.getValue() as number)
                : null
            }
          />
        ),
        enableSorting: true,
      },

      // ΔSlots (copy - source)
      {
        id: "delta_slots",
        header: "ΔSlots",
        accessorFn: (row) => {
          const c = Number(row.copy_slot ?? NaN);
          const s = Number(row.source_slot ?? NaN);
          if (!Number.isFinite(c) || !Number.isFinite(s)) return null;
          return c - s;
        },
        cell: (ctx) => {
          const v = ctx.getValue() as number | null;
          return v == null ? "—" : String(v);
        },
        enableSorting: true,
      },

      // Creator (short hash)
      {
        id: "creator",
        header: "Creator",
        accessorKey: "creator_pubkey",
        cell: (ctx) => {
          const v = ctx.getValue() as string | null | undefined;
          if (!v) return "—";
          return (
            <Link
              className="underline font-mono"
              href={`/creators/${encodeURIComponent(v)}`}
            >
              {hashShort(v)}
            </Link>
          );
        },
      },

      // Actions
      {
        id: "actions",
        header: "",
        cell: (ctx) => {
          const r = ctx.row.original;
          return (
            <div className="flex gap-2">
              <Link
                className="text-xs underline"
                href={`/compare?trade_id=${encodeURIComponent(r.id)}`}
              >
                Compare
              </Link>
              <Link
                className="text-xs underline"
                href={`/ladder?pair_id=${encodeURIComponent(r.id)}`}
              >
                Ladder
              </Link>
            </div>
          );
        },
        enableSorting: false,
      },
    ];
  }, []);
}

/**
 * Utility to normalize backend rows to TradeRow.
 * Pass the raw array returned by /v1/trades.
 */
export function normalizeTrades(rows: any[]): TradeRow[] {
  if (!Array.isArray(rows)) return [];
  return rows.map((r: any) => ({
    id: String(r.trade_id ?? r.id ?? ""),
    // ✅ Add backend 'timestamp' as the first fallback option
    ts: r.timestamp ?? r.ts ?? r.time ?? r.created_at ?? r.block_time ?? null,
    action: String((r.action ?? r.side ?? "") || "").toUpperCase(),
    mint: r.mint ?? r.token ?? r.symbol ?? null,
    amount:
      r.amount ??
      r.size ??
      r.qty ??
      r.quantity ??
      null,
    price:
      r.price ??
      r.avg_price ??
      r.execution_price ??
      null,
    exec_score: r.execution_score ?? r.exec_score ?? null,
    copy_slot: r.copy_slot ?? null,
    source_slot: r.source_slot ?? null,
    creator_pubkey: r.creator_pubkey ?? null,
    wallet_id: r.wallet_id ?? null,
  }));
}


/**
 * LiveTradesTable
 * Drop-in table for the Trades feed or any page showing trade rows.
 * - Provide raw rows from API; we normalize + render.
 * - Client-side sort/paginate; default page size 20.
 */
export function LiveTradesTable({
  rows,
  pageSize = 20,
  initialSortId = "ts",
  initialSortDesc = true,
}: {
  rows: any[];
  pageSize?: number;
  initialSortId?: string;
  initialSortDesc?: boolean;
}) {
  const data = React.useMemo(() => normalizeTrades(rows), [rows]);
  const columns = useColumns();

  return (
    <DataGrid<TradeRow>
      data={data}
      columns={columns}
      pageSize={pageSize}
      initialSorting={[{ id: initialSortId, desc: !!initialSortDesc }]}
      emptyLabel="No trades."
    />
  );
}

export default LiveTradesTable;

/* ---------- (optional) export small helpers if you want to reuse ---------- */
export function fmtLamports(n?: number | null) {
  if (n == null) return "—";
  try {
    return fmtNum(n, 0);
  } catch {
    return "—";
  }
}
